function preprocessImage(canvas) {
  const ctx = canvas.getContext("2d", { willReadFrequently: true });
  let image = ctx.getImageData(0, 0, canvas.width, canvas.height);
  //invertColors(image.data);
  //grayScale(image.data);
  image = binarizeImage(image);
  return image;
}

function invertColors(pixels) {
  for (var i = 0; i < pixels.length; i += 4) {
    pixels[i] = pixels[i] ^ 255; // Invert Red
    pixels[i + 1] = pixels[i + 1] ^ 255; // Invert Green
    pixels[i + 2] = pixels[i + 2] ^ 255; // Invert Blue
  }
}

function thresholdFilter(pixels, level) {
  if (level === undefined) {
    level = 0.5;
  }
  const thresh = Math.floor(level * 255);

  for (let i = 0; i < pixels.length; i += 4) {
    const r = pixels[i];
    const g = pixels[i + 1];
    const b = pixels[i + 2];
    const gray = 0.2126 * r + 0.7152 * g + 0.0722 * b;
    let val;
    if (gray >= thresh) {
      val = 255;
    } else {
      val = 0;
    }
    pixels[i] = pixels[i + 1] = pixels[i + 2] = val;
  }
}

function grayScale(pixels) {
  for (let i = 0; i < pixels.length; i += 4) {
    const avg = (pixels[i] + pixels[i + 1] + pixels[i + 2]) / 3;
    pixels[i] = avg;
    pixels[i + 1] = avg;
    pixels[i + 2] = avg;
  }
}

function binarizeImage(imageData) {
  // Calculate the histogram of the image
  const histogram = Array(256).fill(0);
  for (let i = 0; i < imageData.data.length; i += 4) {
    histogram[imageData.data[i]]++;
  }

  // Calculate the total number of pixels in the image
  const totalPixels = imageData.data.length / 4;

  // Initialize variables for the loop
  let sum = 0;
  let sumB = 0;
  let wB = 0;
  let wF = 0;
  let maxVariance = 0;
  let threshold = 0;

  // Loop through all possible threshold values
  for (let t = 0; t < 256; t++) {
    wB += histogram[t];
    if (wB === 0) continue;
    wF = totalPixels - wB;
    if (wF === 0) break;
    sumB += t * histogram[t];
    const mB = sumB / wB;
    const mF = (sum - sumB) / wF;
    const variance = wB * wF * (mB - mF) ** 2;
    if (variance > maxVariance) {
      maxVariance = variance;
      threshold = t;
    }
  }

  // Binarize the image using the determined threshold value
  const binarizedData = new Uint8ClampedArray(imageData.data);
  for (let i = 0; i < binarizedData.length; i += 4) {
    binarizedData[i] = binarizedData[i] > threshold ? 255 : 0;
    binarizedData[i + 1] = binarizedData[i] > threshold ? 255 : 0;
    binarizedData[i + 2] = binarizedData[i] > threshold ? 255 : 0;
  }

  return new ImageData(binarizedData, imageData.width, imageData.height);
}

export default preprocessImage;
