import { useState, useRef } from "react";
import Tesseract from "tesseract.js";
import "./App.css";
import preprocessImage from "./preprocess";
import { Camera } from "react-camera-pro";
import { useEffect } from "react";

function App() {
  const [imagePath, setImagePath] = useState("");
  const [text, setText] = useState("");
  const [loading, setLoading] = useState(false);

  let { width } = document.body.getBoundingClientRect();

  const canvasRef = useRef(null);
  const camera = useRef(null);

  const capture = () => {
    const pathImg = camera.current.takePhoto();

    let image = new Image();
    image.src = pathImg;
    image.onload = () => {
      const canvas = canvasRef.current;
      const ctx = canvas.getContext("2d");

      ctx.drawImage(image, 0, 0);
      ctx.putImageData(preprocessImage(canvas), 0, 0);
      setImagePath(canvas.toDataURL("image/jpeg"));
      setLoading(true);
      /*Tesseract.recognize(imagePath, "eng", {
        logger: (m) => {
          setLoading(true);
        },
      })
        .catch((err) => {
          setLoading(false);
          alert("Erreur");
        })
        .then((result) => {
          setLoading(false);
          if (result) {
            console.log(result);
            let data = result?.data;
            // Get Confidence score
            let confidence = data?.confidence;

            if (confidence >= 75) {
              let text = data.text;
              setText(text);
            } else {
              alert("Erreur de lecture");
            }
          }
        });*/
      setTimeout(capture, 300);
    };
  };

  useEffect(() => {
    setTimeout(capture, 2000);
  }, [capture]);

  return (
    <div className="App">
      <main className="App-main">
        <Camera ref={camera} facingMode="environment" aspectRatio={40 / 9} />
        <br />
        <button onClick={capture} style={{ display: "none" }}>
          Capture photo
        </button>
        <br />
        <div>
          <h3>Prévisualisation:</h3>
          <canvas
            ref={canvasRef}
            width={width + "px"}
            height={"150px"}
            style={{ margin: "auto" }}
          ></canvas>
        </div>

        <br />
        {loading && (
          <div style={{ width: width + "px" }}>Extraction du texte...</div>
        )}
        {text && (
          <div style={{ width: width + "px" }}>
            Le text extrait est <br /> {text}
          </div>
        )}
      </main>
    </div>
  );
}

export default App;
